 @import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;700&display=swap');
:root{
  --Desaturated_Dark_Cyan: hsl(180, 29%, 50%);
--Light_Grayish_Cyan1: hsl(180, 52%, 96%);
--Light_Grayish_Cyan2: hsl(180, 31%, 95%);
--Dark_Grayish_Cyan: hsl(180, 8%, 52%);
--Very_Dark_Grayish_Cyan: hsl(180, 14%, 20%);
}
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
}


html,
body {
  width: 100%;
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
    /* height: 100%; */
    background-color: var(--Light_Grayish_Cyan1);
    font-family: 'League Spartan', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

nav{
  position: absolute;
  top: 0;
  width: 100%;
  height: 15%;
  background-image: url("../public/images/bg-header-desktop.svg");
  background-position: top;
  background-repeat: no-repeat;
  background-size:contain;
  background-color: var(--Desaturated_Dark_Cyan);
}
form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
}
img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

input,
button,
textarea,
select {
    font: inherit;
    cursor: pointer;
}
option{
  color: var(--Desaturated_Dark_Cyan);
}
select{
  color: var(--Desaturated_Dark_Cyan);
  border: none;
  border-radius: 5px;
  height: 1.5rem;
  margin-right: 1rem;
}
.loading{
  opacity: 0.5;
  background-color: white;
  color: black;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}
p{
  font-size: 1rem;
}
#root,
#__next {
    isolation: isolate;
}
.App{
  
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  /* padding: 0 2rem; */
}
#root{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card{
  border-radius: 0.5rem;
  width: 80%;
  padding: 1rem;
  margin: 1rem;
  background-color: white;
  display: flex;
  justify-content: center;
  box-shadow: 0px 1px 12px var(--Dark_Grayish_Cyan);
  /* background-color: white; */
  
}
.card:first-of-type{
  margin-top:7rem;
}
/* .search{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
} */
.info{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
img{
  padding-right: 2rem;
}
.tool-item{
  font-size: 1rem;
}
nav h2{
  color: white;
}
.company{
  width: 80%;
  display: flex;
  justify-content: flex-start;
}
.company h3{
  color: var(--Desaturated_Dark_Cyan);
}
label{
  color: var(--Light_Grayish_Cyan2);
}
.company p{
  color: white;
  font-size: 0.8rem;
}
.company div:first-of-type{
  background-color: var(--Desaturated_Dark_Cyan);
  padding:5px;
  margin-left: 1rem;
  display: flex;
  height: 1.5rem;
  align-items: center;
  border-radius: 35%;
}
button{
  width: 6rem;
  border-radius: 1rem;
  border: none;
  margin-top: 0.5rem;
}
button:hover{
  background-color: hsl(180, 44%, 79%);
  border: none;
  color: white;
}
.company div:last-of-type{
  background-color: black;
  border-radius: 25%;
  display: flex;
  margin-left: 1rem;
  height: 1.5rem;
  align-items: center;
  padding: 0 5px;
}
p.tool-item{
  color: var(--Desaturated_Dark_Cyan);
  font-weight: bold;
}
.tool-parent{
  background-color: var(--Light_Grayish_Cyan2);
  height: 2rem;
  padding: 5px;
  margin:0 5px;
  border-radius: 5px;
}
.tools{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.extras{
  display: flex;
  justify-content: space-between;
  position: relative;
}
.extras div{
  display: flex;
  padding-right: 2rem;
  align-items:center;
  position: relative;
}
.extras div p{
  color: var(--Dark_Grayish_Cyan);
}
.extras div:not(:last-of-type)::after{
  content: "";
  height: 5px;
  position: absolute;
  width: 5px;
  border-radius: 50%;
  background-color: var(--Dark_Grayish_Cyan);
  right: 15%;
}
.sidelinebar{
  position: relative;
  width: 1%;
}
.sidelinebar div{
  width: 0.3rem;
  margin: -1rem;
  position: absolute;
  height: 135%;
  border-radius: 0.5rem 0 0 0.5rem;
  background-color: var(--Desaturated_Dark_Cyan);
}
section{
  width: 99%;
  display: flex;
  justify-content: space-between;
}
hr{
  display: none;
}
@media screen and (max-width:800px) {
  .info{
    flex-direction: column;
  }
  .tool-parent{
    flex-direction: column;
  }
  .tools{
    flex-wrap: wrap;
  }
  hr{
    display: block;
  }
  section{
    flex-direction: column;
  }
  .card{
    width: 90%;
    margin: 3rem 1rem;
  }
  hr{
    /* background-color: black; */
    margin: 1rem 0;
  }
  .company h3{
    padding: 1rem 0;
  }
  .company{
    align-items: center;
  }
  .info{
    align-items: flex-start;
  }
  .sidelinebar div{
    height: 110%;
  }
  img{
    margin-top: -4rem;
  }
  nav{
    height: 15%;

  }
  label{
    display: flex;
    /* flex-direction: column; */
  }
  select{
    height: 1.5rem;
    width: 4rem;
  }
  .card:first-of-type{
    margin-top:10rem;
  }
  form{
    flex-direction: column;
  }
}